<template>
	<div class="project">
		<div class="project_header">
			<router-link tag="h2" to="/" class="project_load">我的首页</router-link>
			<ul class="project_list">
				<router-link tag="li" to="items">我的项目</router-link>
				<router-link tag="li" to="workbench">工作台</router-link>
				<router-link tag="li" to="document">文档</router-link>
			</ul>
			<div 
				class="project_personal"
				@mouseover="setover"
				@mouseout="setout"
			>
				<div class="project_personal_img">
					<img src="@/assets/logo.png">
					<span>{{ userName }}</span>
				</div>
			 	<ul class="project_personal_list">
			 		<li @click="setLocalStorage"><div>退出</div></li>
			 	</ul>
			</div>
		</div>
		<router-view></router-view>
	</div>
</template>

<script>
export default {
	data: function() {
		return {
			userName: '',
		}
	},
	created: function() {
		var userName = this.$local("keys");
		this.userName = userName.name;
	},
	methods: {
		setover: function() {
			var list = document.getElementsByClassName('project_personal_list')[0];
			list.style.display = 'block';
		},
		setout: function() {
			var list = document.getElementsByClassName('project_personal_list')[0];
			list.style.display = 'none';
		},
		setLocalStorage: function() {
			this.$local("keys", null);
			this.$router.push({name: 'Register'})
		}
	}
}
</script>




