import Vue from 'vue'
import Router from 'vue-router'

import header from '@/components/header'
import content from '@/components/content'
import footer from '@/components/footer'
import register from '@/components/register'

import items from '@/content/items'
import workbench from '@/content/workbench'
import Document from '@/content/document'

Vue.use(Router)

var router = new Router({
  linkActiveClass: 'is_active',
  // mode: 'history',
  routes: [
    {
      	path: '/content',
      	component: content,
        meta: {loagig: true},
      	children: [
      		{
      			path: 'items',
            name: 'Items',
            meta: {loagig: true},
      			component: items
      		},
      		{
      			path: 'workbench',
            meta: {loagig: true},
      			component: workbench
      		},
      		{
      			path: 'document',
            name: 'Document',
            meta: {loagig: false},
      			component: Document
      		},
          {
            path: '',
            redirect: {
              name: 'Items'
            }
          }
      	]
    },
    {
    	path: '/footer',
    	component: footer
    },
    {
      path: '/register',
      name: 'Register',
      component: register,
    },
    {
        path: '*',
        name: 'Header',
        component: header
    },
   
  ]
})

router.beforeEach((to, from, next) => {
  if(to.meta.loagig) {
    var info = router.app.$local("keys");
    if (info.loadin) {
      next();
    } else {
      next({name: 'Register', query: { plan: to.path.slice('1')}})
    }
  } else {
    next();
  }

})


export default router


