<template>
	<div id="app">
		<div class="header">
			<h1 class="header_left">我的首页</h1>
			<router-link v-if="!register" tag="button" to="/register" class="header_right">登录</router-link>
			<div @click="ladingShow" v-if="register" class="header_right header_right_portrait">
				<img src="@/assets/logo.png">
				<div v-if="setQuit" class="setQuit" @click="setLocalStorage">退出</div>
			</div>
		</div>

		<div class="content">
			<h2 class="content_title">一起学习vue-router</h2>
			<ul class="content_list">
				<router-link tag="li" to="content">我的项目</router-link>
				<li @click="goDocument">我的文档</li>
			</ul>
		</div>
	</div>
</template>

<script>
export default {
	data: function(){
		return {
			register: false,
			setQuit: false
		}
	},
	created: function() {
		var inof = this.$local("keys");
		this.register = inof.loadin;
	},
	methods: {
		ladingShow: function() {
			this.setQuit = !this.setQuit
		},
		setLocalStorage: function() {
			this.$local("keys", null);
			this.$router.push({name: 'Register'})
		},
		goDocument:function() {
			this.$router.push({name: 'Document'})
		}
	}
}
</script>




